import { h } from 'preact';

const ExpenseInput = (props: any) => {

	const toggle = (e: Event) => {
        e.preventDefault()
		props.store.enabled.value = !props.store.enabled.value

        if(props.store.enabled.value === false) {
            props.store.amt.value = 0
        } else {
            focusNextElement(e)
        }
	};

    function focusNextElement(e: Event) { 
        let inputs = document.querySelectorAll('input')
        let nextFocusIndex = getNextFocusIndex(inputs, e.target as HTMLElement)
        setTimeout(() => {
            let element = inputs[nextFocusIndex]
            element.focus()
            element.select()
        }, 0)
        
    }

    function getNextFocusIndex(inputs: NodeListOf<HTMLInputElement>, target: HTMLElement) {
        let idx = 0

        for(let i=0; i<inputs.length; ++i) {
            if(inputs[i] == target) {
                // found 
                if(i !== inputs.length - 1) {
                    idx = i+1
                    break
                }
            }
        }
        return idx
    }

    const updateValue = (e: Event) => {
		const element = e.currentTarget as HTMLInputElement
        if(element.value === '') {
            element.value = "0"
            props.store.enabled.value = false
            showErrorBlink(element)
        } else {
            let val = parseInt(element.value)
            if(val > props.min_value) {
                if(val > props.max_value) {
                    showErrorBlink(element)
                    element.value = props.max_value
                } else {
                    showSuccessBlink(element)
                    props.store.amt.value = val
                }
            } else {
                showErrorBlink(element)
                props.store.amt.value = 0
                props.store.enabled.value = false    
            }
        }
	};

    const showSuccessBlink = (element: HTMLInputElement) => {
        let bg = element.style.backgroundColor
        element.style.backgroundColor = 'lightgreen'
        setTimeout(()=>{
            element.style.backgroundColor = bg
        }, 500)
    }

    const showErrorBlink = (element: HTMLInputElement) => {
        let bg = element.style.backgroundColor
        element.style.backgroundColor = 'pink'
        setTimeout(()=>{
            element.style.backgroundColor = bg
        }, 500)
    }

    const handleFrequency = (e: Event) => {
		const element = e.currentTarget as HTMLSelectElement
		props.store.frequency.value = parseInt(element.value)
	};

    const handlePayment = (e: Event) => {
		const element = e.currentTarget as HTMLSelectElement
		props.store.payment.value = parseInt(element.value)
	};

    return (
        <div class="mb-3 border rounded" style="width:450px;padding:5px;">
            <div class="input-group mb-3">
                <span class="input-group-text" style="width:180px;font-size:10pt;">{props.title}</span>
                <div class="input-group-text">
                    <input onChange={toggle} class="form-check-input mt-0" type="checkbox" aria-label="Checkbox for following text input" checked={props.store.enabled.value}/>
                </div>
                <span class="input-group-text">$</span>
                <input onBlur={updateValue} type="number" value={props.store.amt.value} aria-label="Amount" class="form-control text-end" disabled={props.store.enabled.value === false}></input>
                <span class="input-group-text">.00</span>
                <div class="input-group">
                    <div class="form-floating">
                        <select class="form-select" id="floatingSelect" aria-label="Filing status" style="font-size:10pt;width:200px;"
                            value={props.store.frequency.value} onChange={handleFrequency}>
                            <option value="0">Monthly</option>
                            <option value="1">Quarterly</option>
                            <option value="2">Semi-Annually</option>
                            <option value="3">Annually</option>
                        </select>
                        <label for="floatingSelect" style="font-size:9pt;">Frequency</label>
                    </div>
                    <div class="form-floating">
                        <select class="form-select" id="floatingSelect" aria-label="Filing status" style="font-size:10pt;width:239px;"
                            value={props.store.payment.value} onChange={handlePayment}>
                            <option value="0">0% pay with cash/check</option>
                            <option value="1">1% back with credit card</option>
                            <option value="2">2% back with credit card</option>
                            <option value="3">3% back with credit card</option>
                            <option value="4">4% back with credit card</option>
                            <option value="5">5% back with credit card</option>
                            <option value="6">6% back with credit card</option>
                            <option value="7">10% back with credit card</option>
                        </select>
                        <label for="floatingSelect" style="font-size:9pt;">Payment method</label>
                    </div>

                </div>
            </div>
            <label style="font-size:11px;padding-left:4px;">{props.description}</label>
        </div>
    )
}

export default ExpenseInput;