export class InputLogic {
    static focusFirstInputElement() {
        let inputs = document.querySelectorAll('input')
		if(inputs.length > 0) {
			inputs[0].focus()
		}
    }

    static focusFirstSelectElement() {
        let inputs = document.querySelectorAll('select')
		if(inputs.length > 0) {
			inputs[0].focus()
		}
    }
}
