import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import { useEffect } from 'preact/hooks';
import { InputLogic } from '../../input/inputlogic';
import ExpenseInput from '../../components/ExpenseInput';

const Transport = () => {

	useEffect(() => {
		InputLogic.focusFirstInputElement()
	}, []);

    return (
		<div style="padding:10px;">
			<p style="height:50px;"/>

			<div class="container">
				<hr/>
				<h5>Vehicle related</h5>
				Not all the expenses here apply to your situation, only check on those item that is applicable. The expense amount can be estimates. Click next when you are done.<p/>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.vehicle.car_payment} title="Vehicle payment" min_value="0" max_value="10000"
							description="Installment on your vehicle"/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.vehicle.gasoline} title="Gasoline/petro" min_value="0" max_value="10000"
							description="Gasoline at the pump."/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.vehicle.car_tab} title="Car registration/tab" min_value="0" max_value="20000"
							description="Car registration. Normally a yearly fee."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.vehicle.car_maintenance} title="Car maintenance" min_value="0" max_value="20000"
							description="Cost of bringing your vehicle for maintenance or services."/>					
					</div>
				</div>	
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.vehicle.parking} title="Parking" min_value="0" max_value="5000"
							description="Cost of parking."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.vehicle.toll} title="Toll charges" min_value="0" max_value="20000"
							description="Toll charges."/>
					</div>
				</div>	
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.vehicle.insurance} title="Insurance" min_value="0" max_value="5000"
							description="Cost of insurance for your vehicle."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.vehicle.other} title="Other cost" min_value="0" max_value="20000"
							description="Other cost related to your vehicle."/>
					</div>
				</div>	                							
                <hr/>
                    <button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/expense/food')}>Back: Food</button>&nbsp;
                    <button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/expense/lifestyle')}>Next: Life Style</button>
				<hr/>                 
				<h5>Other local transport</h5>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.vehicle.ride_hailing} title="Ride hailing" min_value="0" max_value="10000"
							description="Estimated cost of ride-hailing services such as Uber/Lyft."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.vehicle.public_transport} title="Public transportation" min_value="0" max_value="50000"
							description="Estimated cost of public transportation such as bus, ferry, rail etc."/>
					</div>
				</div>
			</div>
		</div>
		
	);
};

export default Transport;
